@import 'src/styles/vars';

.empty-data {
  margin-top: 20px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  &__title {
    font-size: 14px;
    font-weight: 600;
    color: var(--black);

    @include for-size(tablet) {
      font-size: 16px;
    }

    @include for-size(small-desktop) {
      font-size: 18px;
    }

    @include for-size(desktop) {
      font-size: 20px;
    }
  }
}
