@import './src/styles/vars';

.cards {
  padding-top: 120px;
  padding-bottom: 120px;

  @include for-size(xl) {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  @include for-size(md) {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  .row {
    padding: 0 30px;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 120px;
    column-gap: 90px;

    @include for-size(md) {
      padding: 0;
      margin-bottom: 40px;
      flex-direction: column;
      column-gap: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &:nth-child(even) {
      flex-direction: row-reverse;

      @include for-size(md) {
        flex-direction: column;
      }
    }
  }

  .block {
    width: 470px;

    @include for-size(md) {
      width: 100%;
    }
  }

  .img {
    display: flex;
    //width: 570px;
    width: 480px;
    height: 390px;
    -webkit-box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.8);
    -moz-box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.8);
    box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.8);

    @include for-size(md) {
      width: 100%;
      height: auto;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .content {
    margin-bottom: 40px;
    font-family: 'Sriracha', cursive;
    font-weight: 400;
    //font-family: "Fondamento", cursive;
    //font-style: italic;
    font-size: clamp(14px, 4vw, 20px);
    line-height: 1.6;
    color: #31405b;

    @include for-size(md) {
      margin-bottom: 20px;
    }

    a {
      color: #138ece;
    }
  }

  .header {
    font-family: 'Caveat', cursive;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: clamp(28px, 8vw, 58px);
    color: #31405b;
    text-align: center;

    @include for-size(md) {
      margin-bottom: 10px;
    }
  }
  .button {
    background-color: inherit;
    padding: 5px 40px;
    border: 1px solid #000000;
    font-size: 20px;
    font-weight: 500;
    text-transform: lowercase;

    @include for-size(md) {
      margin-bottom: 10px;
    }
  }
}
