@import './src/styles/vars';

.wrapper {
  height: 100vh;
  padding: 5vh 2vw;
  position: relative;
  //min-height: 600px;
  display: flex;
  flex-direction: column;
  font-size: 70px;
  font-weight: 600;
  //background-image: url('./bg-1.jpg');
  background: #fff;
  overflow-x: hidden;
  //background-repeat: no-repeat;
  //background-position: center center;
  //background: rgb(255,255,2
  //55);
  //background: radial-gradient(circle, rgba(255,255,255,1) 34%, rgba(232,222,208,1) 100%);

  @include for-size(md) {
    height: 600px;
    padding: 20px;
  }

  .container {
    max-width: 1230px;
    width: 100%;
    padding: 40px 15px;
    margin: 0 auto;
    //background-color: wheat;
    background: linear-gradient(to right, #b19d87 85%, #f7f6ee 85%);
    //background: linear-gradient(to right, #b19d87 85%,  85%);

    @include for-size(md) {
      //padding: 20px 15px 0 15px;
      padding: 20px;
      height: 100%;
      //background: #b19d87;
      background: linear-gradient(to bottom, #b19d87 85%, #ffffff 85%);
      display: flex;
      flex-direction: column;
    }
  }

  .mainBlock {
    margin-bottom: 10vh;
    width: 100%;
    display: flex;
    justify-content: space-between;
    user-select: none;

    @include for-size(md) {
      margin-bottom: 0;
    }

    .logo {
      padding-left: 48px;
      font-size: clamp(54px, 12vw, 164px);
      font-family: 'AlkalamiRegular', serif;
      font-weight: 400;

      @include for-size(md) {
        width: auto;
        padding-left: 0;
        margin-bottom: 20px;
      }
    }

    .imagesBlock {
      position: relative;
      width: 50%;
      display: flex;
      justify-content: flex-end;

      @include for-size(md) {
        display: none;
      }

      .firstImg {
        position: absolute;
        max-width: 300px;
        width: 100%;
        top: 200px;
        right: 250px;
        z-index: 1;
        -webkit-box-shadow: -7px 7px 5px -5px rgba(34, 60, 80, 0.6);
        -moz-box-shadow: -7px 7px 5px -5px rgba(34, 60, 80, 0.6);
        box-shadow: -7px 7px 5px -5px rgba(34, 60, 80, 0.6);
      }

      .secondImg {
        position: relative;
        display: flex;
        max-width: 300px;
        width: 100%;
        z-index: 2;
        -webkit-box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.8);
        -moz-box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.8);
        box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.8);
      }
    }
  }

  .mobileImg {
    display: none;

    @include for-size(md) {
      display: block;
      position: absolute;
      top: 300px;
      right: 10px;
      width: 220px;
      z-index: 2;
    }
  }
}
